.Background {

    width: 100vw;
    height: 100vh;
    background: url("https://i.ibb.co/g90JTkv/pexels-elijah-o-donnell-5098976.jpg") no-repeat center 05px/cover;
    overflow: hidden;
}

.Container {
    position: relative;
    width: 300px;
    height: 200px;

    left: 50%;
    transform: translate(-50%);
    top: 330px;
    text-align: center;

}

.Container label {
    display: block;
    margin: 10px;
    color: wheat;
}

.Container input {
    display: block;
    margin: auto;
    width: 200px;
    height: 30px;
}

.Container input:focus {
    outline: none;

}


.Spiner {
    position: absolute;
    top: 300px;
    left: 50%;
    transform: translate(-50%);
}


@media screen and (max-height: 400px) {
    .Container {
        top: 130px;
        color: red;

    }

    .Container input {
        display: inline;
        margin: auto;
        width: 200px;
        height: 30px;
    }

    .Spiner {
        top: 130px
    }


}

@media screen and (max-width: 500px) {
    .Container {
        top: 130px;


    }

    .Spiner {
        top: 130px
    }

}

